* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  overflow-x: hidden;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.skip_link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip_link:focus {
  top: 0;
}

nav {
  display: grid;
  grid-template-columns: 4fr 4fr 1fr;
  height: 3.5rem;
  width: 100%;
  background-color: #ffdd95;
  box-shadow: 0px 5px 8px rgb(162, 158, 158);
  position: fixed;
  top: 0;
  z-index: 3;

  div {
    display: grid;
    height: 100%;
  }
  div {
    place-items: center;
  }
  a {
    text-decoration: none;
  }
  div.title {
    background-color: white;
    font-size: smaller;
  }
  div.penawaran {
    background-color: transparent;
    a {
      font-weight: bold;
      color: white;
      font-size: smaller;
      padding: 1rem;

      background-color: rgb(209, 30, 14);

      &:hover {
        background-color: darken(red, 20%);
      }
    }
  }
  div.hamburger > button {
    width: 2.8rem;
    height: 2.8rem;
    font-size: x-large;
    background-color: transparent;
    border-radius: 0.4rem;
  }

  div.drawer {
    background-color: #ffdd95;
    position: absolute;
    z-index: 1;
    width: 12rem;
    height: 14rem;
    top: 3.5rem;
    right: 0;
    gap: 1rem;
    padding: 1rem;
    transform: translate(12rem);
    display: none;
    text-align: left;
    a {
      width: 100%;
      padding: 1rem;
      font-size: small;
      font-weight: bold;
      color: black;

      &:hover {
        background-color: lighten(#ffdd95, 10%);
      }
    }
  }
  .drawer.open {
    transform: translate(0);

    width: 100%;
    display: grid;
  }
}

#jumbotron {
  width: 100%;
  background-image: url(../public/images/heros/hero-image_2.jpg);
  height: 20rem;
  background-size: cover;
  filter: brightness(80%);

  .hero_content {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    text-align: center;
    filter: brightness(2);
    padding: 6rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  h2 {
    color: white;
    font-size: 24px;
  }
  p {
    color: #865f0b;
    font-size: 14px;
    font-weight: bold;
    background-color: aliceblue;
    max-width: 34rem;
  }
  .hero_button {
    font-size: 14px;
    padding: 0.5rem 0;
    display: flex;
    font-weight: bolder;
    max-width: 34rem;
    gap: 1rem;
    justify-content: center;
    :nth-child(1) {
      padding: 0.5rem;
      color: rgb(137, 139, 141);
      text-decoration: none;
      background-color: #865f0b;

      &:hover {
        background-color: darken(#865f0b, 10%);
      }
    }
    :nth-child(2) {
      padding: 0.5rem;
      color: #865f0b;
      text-decoration: none;
      background-color: #898b8d;

      &:hover {
        background-color: darken(#898b8d, 10%);
      }
    }
  }
}

#catalog {
  margin: 2rem auto;
  padding: 0;
  .catalog_title {
    height: 4rem;
    margin: 2rem 0;
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    h3 {
      font-size: 26px;
    }
    div {
      width: 4rem;
      height: 0.5rem;
      background-color: #ffdd95;
      border-radius: 20px;
    }
  }
  .catalog_container {
    display: grid;
    padding: 0 2rem;
    gap: 1.5rem;
    .catalog_card {
      box-shadow: -2px 2px 8px rgb(162, 158, 158);
      border-top-left-radius: 20px;
      border-bottom-right-radius: 20px;
      background: linear-gradient(to right, #ffffff 20%, #ffdd95 80%);
      img {
        border-top-left-radius: 20px;
        width: 100%;
      }
      div {
        margin: 0.5rem 1rem;
        display: grid;
        gap: 0.3rem;

        button {
          height: 2.8rem;
          width: 30%;
          margin: 0 auto;
          background-color: red;
          border: 0;
          color: white;
          font-weight: bold;
          &:active {
            background-color: darken(red, 20%);
          }
        }
      }
    }
  }
}

#contact {
  margin: 2rem auto;
  padding: 1rem;
  .contact_title {
    height: 4rem;
    margin: 2rem 0;
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    h3 {
      font-size: 26px;
    }
    div {
      width: 4rem;
      height: 0.5rem;
      background-color: #ffdd95;
      border-radius: 20px;
    }
  }
  form {
    width: 100%;
    border-radius: 20px;
    box-shadow: -2px 2px 8px rgb(162, 158, 158);
    background: linear-gradient(to right, #ffffff 20%, #ffdd95 80%);
    padding: 1rem;
    display: grid;
    gap: 1rem;

    div {
      display: flex;
      flex-direction: column;

      input,
      button {
        height: 2.8rem;
      }
      input,
      textarea {
        border: 1px solid #ccc; /* Example default border style */
      }

      button {
        font-weight: bold;
        background-color: red;
        border: none;
        color: white;
      }
    }
  }
}

footer {
  width: 100%;
  height: 5rem;
  text-align: center;
  color: #cfcaca;
  font-weight: bold;
  font-size: 12px;
  background-color: black;
  line-height: 5rem;
}

@media screen and (min-width: 768px) {
  nav {
    grid-template-columns: 2fr 4fr 1fr;
    div.title {
      background-color: white;
      font-size: medium;
    }
    div.penawaran {
      background-color: white;
      margin-right: 1rem;
      padding: 0 1rem;
      &:hover {
        background-color: lighten(#ffdd95, 18%);
      }

      a {
        font-weight: bold;
        color: #ffdd95;
        padding: 1rem;
        background-color: transparent;

        &:hover {
          background-color: inherit;
        }
      }
    }

    div.drawer {
      display: grid;
      position: relative;
      width: 70%;
      height: 100%;
      top: auto;
      right: auto;
      gap: 0.5rem;
      padding: 0;
      transform: translate(0);
      transition: transform 0.3s ease-in-out;
      grid-template-columns: 1fr 1fr 1fr;
      text-align: center;

      a {
        padding: 1rem;
        color: black;
        font-weight: bold;
        font-size: smaller;
      }
    }

    div.hamburger {
      display: none;
      background-color: transparent;
    }
  }

  #jumbotron {
    height: 30rem;

    .hero_content {
      width: 80%;
      height: 100%;
      margin: 0 auto;
      text-align: left;
      filter: brightness(2);
      padding: 10rem 0;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }

    h2 {
      color: white;
      font-size: 40px;
    }
    p {
      color: #865f0b;
      font-size: 26px;
      font-weight: bold;
    }
    .hero_button {
      justify-content: start;
      gap: 1rem;
    }
  }

  #catalog {
    margin: 2rem auto;
    padding: 1rem;
    max-width: 1200px;

    .catalog_title {
      height: 5rem;
      margin: 2rem 0;
      padding: 0.5rem 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: start;
      h3 {
        font-size: 40px;
      }
      div {
        width: 4rem;
        height: 0.5rem;
        background-color: #ffdd95;
        border-radius: 20px;
      }
    }
    .catalog_container {
      display: grid;
      padding: 0;

      grid-template-columns: 1fr 1fr 1fr;
      .catalog_card {
        background-color: #ffdd95;
        img {
          width: 100%;
          max-height: 200px;
        }
      }
    }
  }

  #contact {
    margin: 2rem auto;
    padding: 1rem;
    max-width: 1200px;
    .contact_title {
      height: 5rem;
      margin: 2rem 0;
      padding: 0.5rem 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: start;
      h3 {
        font-size: 40px;
      }
      div {
        width: 4rem;
        height: 0.5rem;
        background-color: #ffdd95;
        border-radius: 20px;
      }
    }
    form {
      width: 100%;
      border-radius: 20px;
      box-shadow: -2px 2px 8px rgb(162, 158, 158);
      background: linear-gradient(to right, #ffffff 20%, #ffdd95 80%);
      padding: 1rem;
      display: grid;
      gap: 1rem;

      div {
        display: flex;
        flex-direction: column;

        input,
        button {
          height: 2rem;
        }
        input,
        textarea {
          border: 1px solid #ccc; /* Example default border style */
        }

        button {
          font-weight: bold;
          background-color: red;
          border: none;
          color: white;
        }
      }
    }
  }
}
